.select {
  height: 26px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-radius: 10px;
  background: #f4f4f4;
  margin-right: 4px;
  padding: 6px 0 6px 12px;
  font-size: 12px;
  line-height: 14px;
  color: #333333;
  font-weight: bold;
  margin-right: 10px;
  margin-top: 10px;
}
.selectIcon {
  padding: 5px 12px 5px 5px;
  margin-left: 10px;
  cursor: pointer;
}
